            <h1 class="showcase-title">
              {{ $t('index.showcase.heading') }}
            </h1>
            <p class="showcase-text">
              {{ $t('index.showcase.text') }}
            </p>
            <div>
              <v-btn color="primary" class="mr-3" link to="/blog">{{
                $t('index.showcase.button1.text')
              }}</v-btn>
              <v-btn color="secondary" outlined link to="/about">{{
                $t('index.showcase.button2.text')
              }}</v-btn>
