<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters('image', ['getImage']),
    bgImage() {
      if (this.getImage('showcase-background-image')) {
        return this.getImage('showcase-background-image')
      } else {
        return '/showcase.jpg'
      }
    }
  }
}
</script>

